html {
  height: 100%;
  overflow: hidden; }

body {
  margin: 0;
  padding: 0;
  perspective: 1px;
  transform-style: preserve-3d;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  font-family: Nunito; }

h1 {
  font-size: 250%; }

p {
  font-size: 140%;
  line-height: 150%;
  color: #333; }

.slide {
  position: relative;
  padding: 25vh 10%;
  min-height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  box-shadow: 0 -1px 10px rgba(0, 0, 0, 0.7);
  transform-style: inherit; }

img {
  position: absolute;
  top: 30%;
  left: 70%;
  transform: translateZ(0.25px) scale(0.75) translateX(-94%) translateY(-100%) rotate(2deg);
  padding: 10px;
  border-radius: 5px; }

img:last-of-type {
  transform: translateZ(0.4px) scale(0.6) translateX(-104%) translateY(-40%) rotate(-5deg); }

.andy {
  width: 320px;
  height: 70%; }

.fairy {
  width: 320px;
  height: 40%; }

.lake {
  width: 30%;
  height: 30%;
  top: 40%; }

.slide:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.7); }

.title {
  width: 50%;
  padding: 5%;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.7);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.7); }

.slide:nth-child(2n) .title {
  margin-left: 0;
  margin-right: auto; }

.slide:nth-child(2n+1) .title {
  margin-left: auto;
  margin-right: 0; }

.slide, .slide:before {
  background: 50% 50% / cover; }

.header {
  text-align: center;
  font-size: 175%;
  color: #fff;
  text-shadow: 0 2px 2px #000; }

#title {
  background-image: url("https://images6.alphacoders.com/102/1020776.jpg");
  z-index: 2; }

#endtitle {
  background-image: url("https://www.vicksdesign.com/products/back-view-car-parked-outside-on-road-red-car-back-view-parked-on-road-in-forest-travel-adventure-41-B1.jpg");
  z-index: 2; }

#endtitle h1 {
  transform: translateZ(0.25px) scale(0.75);
  transform-origin: 50% 100%; }

#title h1 {
  transform: translateZ(0.25px) scale(0.75);
  transform-origin: 50% 100%; }

#slide1:before {
  background-image: url("data:image/jpeg;base64,");
  transform: translateZ(-1px) scale(2); }

#slide2 {
  background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRTJ82kvH5Tl0Rsfci8FTiYV5xMNugDhp0tQQYTDXjYrPp0QEBT");
  z-index: 2; }

#slide3:before {
  background-image: url("https://static01.nyt.com/images/2018/04/26/t-magazine/26tmag-yakushima-slide-6F6U/26tmag-yakushima-slide-6F6U-videoSixteenByNineJumbo1600.jpg");
  transform: translateZ(-1px) scale(2); }

#slide4 {
  background-image: url("https://t3.ftcdn.net/jpg/02/58/66/76/240_F_258667644_2I65T8l3Xfwfzaiwy41CvfRSg9kz38Zv.jpg");
  z-index: 2; }

#slide5:before {
  background-image: url("https://cdnb.artstation.com/p/assets/images/images/007/217/077/large/nino-zorc-4.jpg?1504542342");
  transform: translateZ(-0.5px) scale(2); }

